import React from 'react';
import { Link } from 'react-router-dom';
import './Home.css'

const Home = () => {
    return (
        <div>
            <h1>Slave's Work</h1>
            
            <section className="container">
                <ul>
                    <li>
                        <Link to={{
                            pathname: "/host"
                        }} className="HostButton">Host</Link>
                    </li>
                    <li>
                        <Link to={{
                            pathname: "/client"
                        }} className="clientButton">Client</Link>
                    </li>
                </ul>
            </section>
        </div>
    )
}

export default Home;