import React, { useState, useEffect } from 'react';


const useConnect = () => {
    const [devices, setDevices] = useState([]);
    
    const addDevices = () => {
        setDevices([...devices, { key: 1, name: 'DeskTop', status: '100ms' }]);
    }

    return { devices, addDevices };
}

const Connect = () => {
    const { devices, addDevices } = useConnect()
    // console.log(dev)
    return (
        <div>
            <h2>Connects</h2>
        
            <ul>
                {devices.map(device => (
                    <li key={device.key}>
                        <p>{device.name}</p>
                        <p>{device.status}</p>
                    </li>
                ))}
            </ul>
        </div>
    );
}

export default Connect;